import { diff } from "jest-diff";
import {
  GREEN,
  RED,
  RESET,
  isNode,
  state
} from "./common.js";
function test(name, run2) {
  const test2 = {
    name,
    run: run2,
    index: state.index++,
    assertion: [],
    pass(arg) {
      if (typeof arg === "string")
        return isNode ? console.log(`${GREEN}(pass) ${arg}${RESET}`) : console.log(`%c(pass) ${arg}`, "color: green");
      let { operator, title } = arg;
      state.assertIndex++;
      isNode ? console.log(`  ${GREEN}\u2714${RESET} ${title}`) : console.log(`%c\u2714 ${title}`, "color: green");
      test2.assertion.push({
        index: state.assertIndex,
        title
      });
      state.passed++;
    },
    fail(arg) {
      state.assertIndex++;
      if (typeof arg === "string")
        return console.error(arg);
      else if (arg.name !== "Assertion")
        return console.error(arg);
      let { operator, title, ...info } = arg;
      isNode ? (console.log(`  ${RED}\xD7 ${title}`), // ${state.assertIndex} -
      info && "actual" in info && console.info(diff(info.expects, info.actual), RESET)) : (
        // console.info(`actual:${RESET}`, typeof info.actual === 'string' ? JSON.stringify(info.actual) : info.actual, RED),
        // console.info(`expects:${RESET}`, typeof (info.expects ?? info.expected) === 'string' ? JSON.stringify(info.expects ?? info.expected) : (info.expects ?? info.expected), RED),
        // console.info(RESET) // console.error(new Error, RESET)
        info ? (console.log(`%c\xD7 ${title}%c`, "color:red", "color:inherit", info), console.log(diff(info.expects, info.actual))) : console.assert(false, `${state.assertIndex} - ${title}`)
      );
      test2.assertion.push({
        index: state.assertIndex,
        title,
        info,
        error: new Error()
      });
    }
  };
  state.queue = state.queue.then(async (prev) => {
    isNode ? console.log(`${RESET}\u25BA ${test2.name}`) : console.group(test2.name);
    let result;
    try {
      state.currentTest = test2;
      result = await test2.run(test2.pass, test2.fail);
      await new Promise((r) => setTimeout(r));
    } catch (e) {
      test2.fail(e);
      state.failed++;
    } finally {
      state.currentTest = null;
      if (!isNode)
        console.groupEnd();
    }
    return test2;
  });
}
let nestedRuns = 0;
async function run(fn) {
  if (fn) {
    nestedRuns++;
    await fn();
    nestedRuns--;
  }
  if (nestedRuns)
    return;
  try {
    state.start();
    await state.queue;
    console.log(`---`);
    if (state.passed)
      console.log(
        `${isNode ? GREEN : ""}Pass ${state.passed}${isNode ? RESET : ""}`
      );
    if (state.failed)
      console.log(
        `${isNode ? RED : ""}Fail ${state.failed}${isNode ? RESET : ""}`
      );
    if (isNode)
      process.exit(state.failed ? 1 : 0);
  } catch (e) {
    console.error(e);
    if (isNode)
      process.exit(1);
  }
}
export {
  run,
  test
};
